import React from "react";
import Modal2 from "./Modal2";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import BookmarkRemoveIcon from "@mui/icons-material/BookmarkRemove";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

const List = (props) => {
  return (
    <div className="container container-height">
      <div className="grid">
        {props.arr.length === 0 ? <div>No notes available</div> : null}
        {props.arr.reverse().map((val) => {
          const line = !val.status ? "line-through" : "none";
          if (val.status === true) {
            return (
              <div
                className="note item1"
                style={{ backgroundColor: val.color }}
              >
                <div className="done disHide">
                  <OverlayTrigger
                    placement="top"
                    delay={{ show: 1000, hide: 400 }}
                    overlay={<Tooltip id="add-tooltip">Mark done</Tooltip>}
                  >
                    <CheckCircleOutlineIcon
                      className="left btns"
                      fontSize="small"
                      onClick={() => {
                        props.done(val.id);
                      }}
                    />
                  </OverlayTrigger>
                </div>

                <div
                  className="textPart p-2"
                  style={{ textDecoration: line }}
                  onClick={() => {
                    props.edit(val.id);
                    props.handleShow();
                  }}
                >
                  {val.name.length > 135
                    ? val.name.substring(0, 135) + "..."
                    : val.name}
                </div>

                <div className="center">
                  <span className="date p-2">
                    {val.date} | {val.time}
                  </span>
                  <div>
                    <div className="noteBtn center">
                      <OverlayTrigger
                        placement="bottom"
                        delay={{ show: 1000, hide: 400 }}
                        overlay={<Tooltip id="add-tooltip">Delete</Tooltip>}
                      >
                        <DeleteIcon
                          className="right m-1 btns"
                          onClick={() => {
                            props.del(val.id);
                          }}
                        />
                      </OverlayTrigger>
                      <OverlayTrigger
                        placement="bottom"
                        delay={{ show: 1000, hide: 400 }}
                        overlay={<Tooltip id="add-tooltip">Edit</Tooltip>}
                      >
                        <EditIcon
                          className="right m-1 btns"
                          onClick={() => {
                            props.edit(val.id);
                            props.handleShow();
                          }}
                        />
                      </OverlayTrigger>
                      <div className="disHide2">
                        <OverlayTrigger
                          placement="top"
                          delay={{ show: 1000, hide: 400 }}
                          overlay={
                            <Tooltip id="add-tooltip">Mark done</Tooltip>
                          }
                        >
                          <BookmarkRemoveIcon
                            className="btnGap btns"
                            onClick={() => {
                              props.done(val.id);
                              props.setnote("");
                            }}
                          />
                        </OverlayTrigger>
                      </div>
                    </div>
                  </div>
                  <Modal2
                    add={props.add}
                    val={val}
                    handleClose={props.handleClose}
                    handleShow={props.handleShow}
                    showModal={props.showModal}
                    change={props.change}
                    note={props.note}
                    color={props.color}
                    setColor={props.setColor}
                    getColor={props.getColor}
                  />
                </div>
              </div>
            );
          } else {
            return null;
          }
        })}
      </div>
    </div>
  );
};

export default List;
